'use strict'
import Vue from 'vue'
export default function treeToArray(data, expandAll, parent, level, item) {
  let tmp = []
  let _level = 0
  Array.from(data).forEach(function(record) {
    if (record.nodeIds) {
      _level = record.nodeIds.length - 1
    }
    Vue.set(record, '_expanded', true)
    Vue.set(record, '_show', true)
    Vue.set(record, '_load', false)
    Vue.set(record, '_level', _level)
    tmp.push(record)
    if (record.childs && record.childs.length > 0) {
      const childs = treeToArray(record.childs, expandAll, record, _level, item)
      tmp = tmp.concat(childs)
    }
  })
  return tmp
}



