 <!--商品分类列表-->
<template>
  <div>
    <div class="cms-list-layout">
      <cms-prompt collapse='true'>
        <li>显示商品分类列表信息</li>
        <li>可对商品分类进行新增、修改、删除及查看、新增下级商品分类</li>
      </cms-prompt>
      <section class="cms-list-query">
        <div class="query-group">
          <label>分类名称</label>
          <el-input v-model="queryParams.cateName"></el-input>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query" >查询</cms-button>
        </div>
      </section>
      <section class="cms-batch">
        <cms-button v-prem="'/store/category/save'" icon="add" type="success" size="small" 
         @click="$routerLink('/store/category/save',$route.query.type,{nodeIds:'0'})">添加分类
        </cms-button>
      </section>
      <el-table :data="tableData" :row-style="showRow" style="width:100%" v-loading='loading'>
        <el-table-column label="分类名称" min-width="200">
          <template slot-scope="scope">
            <div class="menu-name" :style="{marginLeft:(scope.row._level*16)+'px'}" v-if="iconShow(scope.$index,scope.row)" @click="toggleExpanded(scope.$index)">
              <span class="tree-ctrl">
                <i v-if="scope.row._expanded" class="el-icon-caret-right"></i>
                <i v-else class="el-icon-caret-bottom"></i>
              </span>
              <span>{{scope.row.cateName}}</span>
            </div>
            <div v-else :style="{marginLeft:(scope.row._level*16)+'px'}">
              <span class="icon-dot">○</span>
              <span>{{scope.row.cateName}}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="排序" prop="sortNum">
          <template slot-scope="scope">
            <span>{{scope.row.sortNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="是否显示" prop="isShow">
          <template slot-scope="scope">
            <cms-switch v-model="scope.row.isShow" @change="switchShow(scope.row)"></cms-switch>
          </template>
        </el-table-column>
        <el-table-column label="是否显示店铺导航栏" prop="isTop" min-width="200"  align="center">
          <template slot-scope="scope">
            <cms-switch v-model="scope.row.isTop" @change="switchShow(scope.row)"></cms-switch>
          </template>
        </el-table-column>
        <el-table-column prop="updateTime" label="更新时间" min-width="160">
          <template slot-scope="scope">
            {{$moment(scope.row.updateTime)}}
          </template>
        </el-table-column>
        <el-table-column label="操作" width='280' fixed="right" align="right">
          <template slot-scope="scope">
            <cms-button icon='add' size='small' type='info' v-if="scope.row.nodeIds.length<2"
              @click="$routerLink('/store/category/save',$route.query.type,{storeId:$route.query.id,nodeIds:scope.row.nodeIds.toString()})"
            >新增下级分类
            </cms-button>
            <cms-button v-prem="'/store/category/update'" icon='edit' size='small' type='info' 
             @click="$routerLink('/store/category/update','update',{id:scope.row.id,nodeIds:scope.row.nodeIds.toString()})">编辑
            </cms-button>
            <cms-button icon='delete' size='small' type='info' @click.stop="deleteData(scope.row,scope.$index)">删除</cms-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import treeToArray from '@/utils/eval.js'
import {
  categorylist,
  categoryDelete,
  categoryUpdate
} from '@/api/shop/shopManage.js'

import list from '@/mixins/list'
export default {
  mixins: [list],
  data() {
    return {
      typeList: [],
      tableData: [],
      expandAll: false,
      queryParams: {
        cateName: ''
      },
      selfId: 0, // 当前点击的id 不可删除
      columnState: true // 判断行隐藏
    }
  },
  computed: {
    backUrl() {
      return this.$route.query.type === 'selfShop' ? '/selfShops/list' : '/shopManage/list'
    }
  },
  methods: {
    showRow: function(row) {
      const show = row.row._show
      row.row._show = show
      return show ? 'animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;' : 'display:none;'
    },
    toggleExpanded: function(trIndex) {
      const record = this.tableData[trIndex]
      if (record._expanded) {
        // 判断展开
        if (!record._load) {
          // 判断是否加载过
          this.queryParams.parentId = record.id
          this.loading = true
          categorylist(this.queryParams).then(res => {
            if (res.code === 200) {
              const arr = treeToArray(res.data)
              for (let i = 0; i < arr.length; i++) {
                this.tableData.splice(trIndex + (i + 1), 0, arr[i])
              }
              record._expanded = false
              record._load = true
              this.loading = false
            }
          })
        } else {
          this.chidView(record, true)
          record._expanded = false
        }
      } else {
        record._expanded = true
        this.chidView(record, false)
      }
    },
    switchShow(item) { // 滑块的事件
      console.log(item)
      categoryUpdate(item).then(res => {
        if (res.code === 200) {
          this.$successMessage('修改成功')
        } else {
          this.$errorMessage(res.code)
        }
      })
    },
    chidView(record, state) {
      // 子集显示，核心判断这个方法
      const arr = []
      Array.from(this.tableData).forEach(v => {
        if (v.nodeIds.indexOf(record.id) > -1 && v.id !== record.id) {
          v._show = state
        }
        arr.push(v)
      })
      this.tableData = arr
    },
    // 图标显示
    iconShow(index, record) {
      return !record.isChild
    },
    getTableData(callBack, params) {
      this.loading = true
      callBack(params).then(res => {
        if (res.code === 200) {
          this.tableData = treeToArray(res.data)
          this.loading = false
        }
      })
    },
    deleteData(row, index) {
      // 删除菜单
      this.$confirm('是否确定删除店铺商品分类?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      })
        .then(() => {
          this.loading = true
          categoryDelete({ ids: [row.id] }).then(res => {
            this.loading = false
            if (res.code === 200) {
              if (row.isChild) {
                this.tableData.splice(index, 1)
              } else {
                this.queryParams.parentId = ''
                this.getTableData(categorylist, this.queryParams)
              }
              this.$successMessage('删除成功')
            } else {
              this.$errorMessage('删除失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    addNew() {
      this.$router.push({
        path: '/store/category/save',
        query: { storeId: this.$route.query.id }
      })
    },
    query() {
      this.getTableData(categorylist, this.queryParams)
    },
    getTypeList() {
      this.getTableData(categorylist, this.queryParams)
    }
  },
  activated() {
    this.getTableData(categorylist, this.queryParams)
  },
  created() {
    this.getTableData(categorylist, this.queryParams)
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
$color-blue: #188ae2;

.menu-name {
  position: relative;
  cursor: pointer;
  user-select: none;
}
.tree-ctrl {
  cursor: pointer;
  color: $color-blue;
  margin-left: 0px;
}
.icon-dot {
  width: 14px;
  height: 14px;
  display: inline-block;
  position: relative;
  top: 1px;
  left: 4px;
  color: $color-blue;
  font-size: 18px;
  font-weight: bold;
}
</style>



